.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.movie-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.6em;
}
.movie-wraper{
  background-color: antiquewhite;
  width: 80%;
  width: 300px;
  color: black;
  font-weight: 500;
  margin-bottom: 10px;
}
.movie-search {
  height: 24px;
  font-size: 16px;
  /* margin-bottom: 32px; */
  font-weight: bold;
  padding: 12px;
}
.button-search {
  padding-top: 12px;
  padding-bottom: 12px;
  border-radius: 8px;
}
.search {
  display: flex;
  justify-content: center;
  align-items: center ;
  gap: 10px;
  margin-bottom: 24px;
}
.movie-img{
  max-width: fit-content;
  
}
.year{
  font-size: 24px;
  margin-bottom: 24px;
  color: red;
  padding-bottom: 24px;
}